<template>
  <div>
    <Tab :items="tabs" />
    <template v-if="isEmptyCarriers">
      <ShippingProviderEmptyCarrier
        @action="handleSetupAccount"
        shipping-type="Shipmondo"
      />
    </template>
    <template v-else>
      <div
        :style="{ maxHeight: '50vh', overflowY: 'auto', paddingRight: '10px' }"
      >
        <RouterView v-slot="{ Component, route }">
          <template v-if="Component">
            <Transition name="slide-fade" :key="route.name">
              <KeepAlive>
                <Suspense>
                  <template #default>
                    <component
                      ref="componentElement"
                      :is="Component"
                      :key="route.name"
                    ></component>
                  </template>
                  <template #fallback>
                    <div class="w-full flex items-center justify-center py-12">
                      <BaseLoading />
                    </div>
                  </template>
                </Suspense>
              </KeepAlive>
            </Transition>
          </template>
        </RouterView>
      </div>
    </template>
  </div>
</template>
<script>
import { computed, defineComponent, ref } from 'vue'
import {
  useConfig,
  useShippingProvider,
  useShippingKit
} from 'composable/integration/shipmondo'

import Tab from 'bbUi/components/tab/tab.vue'

import ShippingProviderEmptyCarrier from 'components/Integration/ShippingProviderEmptyCarrier.vue'
import BaseLoading from 'components/BaseLoading.vue'

export default defineComponent({
  components: {
    Tab,
    ShippingProviderEmptyCarrier,
    BaseLoading
  },
  async setup() {
    const { carriers, fetchAll: fetchAllShippingProvider } =
      useShippingProvider()
    const { fetchShippingKit } = useShippingKit()
    const { tabs } = useConfig()
    const componentElement = ref(null)
    const isEmptyCarriers = computed(() => carriers.value.length === 0)
    const handleSetupAccount = () => {
      window.open('https://app.shipmondo.com', '_blank', 'noreferrer noopener')
    }

    await Promise.all([fetchAllShippingProvider(), fetchShippingKit()])
    return {
      tabs,
      componentElement,
      isEmptyCarriers,
      handleSetupAccount
    }
  }
})
</script>
<style lang="scss" scoped>
.loading-logo {
  animation: rotate 0.8s infinite linear;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
</style>
